// ? Componentes de react
import React from 'react';
// ~ Componentes de Gatsby
import { GatsbyImage } from 'gatsby-plugin-image';
// ¬ Importar para manejar el query hecho en GraphiQL
import { graphql, useStaticQuery } from 'gatsby';
// ¿ Estilos
import styled from '@emotion/styled';
import { css } from '@emotion/react';


const ContenidoNosotros = () => {

  const informacion = useStaticQuery( graphql`
  {
    allDatoCmsPagina(filter: {slug: {eq: "nosotros"}}) {
      nodes {
        titulo
        subtitulo
        contenido
        imagen {
          gatsbyImageData(
            layout: FULL_WIDTH
            )
        }
      }
    }
  }
`);

  // Destructuring al contenido obtenido
  const { titulo, subtitulo, contenido, imagen } = informacion.allDatoCmsPagina.nodes[ 0 ];

  return (
    <>
      <h2
        css={ css`
        margin-top: 4rem;
        text-align: center;
        font-size: 4rem;
        `}
      >{ titulo }</h2>
      <h3
        css={ css`
        text-align: center;
        `}
      >{ subtitulo }</h3>

      <Contenido>
        <p>{ contenido }</p>
        <ImageGatsby image={ imagen.gatsbyImageData } alt={ titulo } />
      </Contenido>

    </>
  );
}

const ImageGatsby = styled( GatsbyImage )`
  @media (min-width: 768px) {
      max-width: 120rem;
  }
`;

const Contenido = styled.main`
  padding-top: 4rem;
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;

  @media (min-width: 768px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 3rem;

      p{
        line-height: 2;
        margin-top: 2rem;
      }
  }
`;

export default ContenidoNosotros;